<template>
  <div id="app">
    <loading></loading>
    <router-view/>
  </div>
</template>
<script>
import scrollDreamAnimation from "@/assets/js/scrollDream";
import loading from "@/components/loading";

export default {
  mounted() {
    this.$nextTick(() => {
      window.onload = () => {
        scrollDreamAnimation();
        setTimeout(() => {
          // remove loading
          let loading = document.querySelector("div.loader-wrapper");
          loading.parentElement.removeChild(loading);

          let body = document.querySelector("body.loading-fixed");
          body.className = "";
        }, 500);
      };
    });
  },
  components: {
    loading
  }
};
</script>

